.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #003d13;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    z-index: 100;
}

.logo {
    display: flex;
    align-items: center;
}

.nav {
    display: flex;
    gap: 15px;
}

.navLink {
    color: white;
    text-decoration: none;
    font-weight: bold;
    padding: 5px 10px;
}